.form {
  max-width: 460px;
  margin: 0 auto;
  margin-bottom: 30px;
  padding: 20px;
  border-radius: 10px;
  border: 1px solid #cccccc;
  background-color: #ffffff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
}

.label {
  display: block;
  margin-bottom: 10px;
  font-size: 20px;
}

.input {
  display: block;
  padding-left: 10px;
  width: calc(100% - 10px);
  padding-top: 6px;
  padding-bottom: 6px;
  border-radius: 10px;
  font-size: 16px;
  border: 1px solid #cccccc;
  outline: none;
}

.input:focus {
  border: 1px solid #4c70e6;
}

.addBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 120px;
  height: 40px;
  border-radius: 10px;
  font-size: 16px;
  background-color: #4c70e6;
  color: #ffffff;
  border: none;
  /* border: 1px solid #ffffff; */
}

.addBtn:hover,
.addBtn:focus {
  background-color: #3e5ab9;
  color: #ffffff;
}
